import React from 'react';

const Notification = ({ type, message }) => {
    const notificationStyle = {
        color: type === 'Success' ? '#00dd00' : '#dd0000',
        backgroundColor: type === 'Success' ? '#e8ffe8' : '#ffe8e8',
        border: `1px solid ${type === 'Success' ? '#00dd00' : '#dd0000'}`,
        borderRadius: '16px',
        padding: '8px',
        width: '50%',
        textAlign: 'center',
        margin: '0 auto'
    };

    if (message === null) {
        return null;
    }

    return (
        <div style={notificationStyle}>
            {message}
        </div>
    );
};

export default Notification;
